 .question {
   background: #fefefe;
   border-radius: 8px;
   box-shadow: 0 2px 12px rgba (0,0,0,0.04);
   padding: 24px;
   

   & + .question {
     margin-top: 8px
   }

   &.highlighted {
    background: #fefefe;
    border: 1.5px solid #00d3c3;

    footer .user-info span {
      color: #29292e ;
    }
   }

   &.answered {
    background: #DBDCDD;
   }

   p {
     color: #29292e;
   }

    footer {
     display: flex;
     justify-content: space-between;
     align-items: center;
     margin-top: 24px;


     .user-info {
        display: flex;
        align-items: center;
        

        img {
          width: 32px;
          height: 32px;
          border-radius: 50%;
        }

        span {
          margin-left: 8px;
          color: #737380;
          font-size: 14px;
        }
     }

     > div {
       display: flex;
       gap: 10px;
     }

     button {
       border: 0;
       background: transparent;
       cursor: pointer;
       transition: filter 0.2s;


       &.like-button {
         display: flex;
         align-items: flex-end;
         color: #737380;
         gap: 8px;
         

         &.liked {
           color: #00d3c3;

           svg path {
             stroke: #00d3c3;
           }
         }
       }

       &:hover {
        filter: brightness(0.6);
       }
     }
    }
   


 }