#page-auth {
  display: flex;
  align-items: stretch;
  height: 100vh;

  aside {
    flex: 7;

    background: #00d3c3;
    color: #1b292f;

    display: flex;
    flex-direction: column;
    justify-content: center;

    padding: 120px 80px;

    img {
      max-width: 320px;
    } 
    strong {
      font: 700 35px 'Poppins', sans-serif;
      line-height: 42px;
      margin-top: 16px;
    }
    
    p {
      font-size: 22px;
      line-height: 32px;
      margin-top: 16px;
      color: #1b292f;

    }
  }

  main {
    flex: 8;
    
    padding: 0 32px;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  .main-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 320px;
    align-items: stretch;
    text-align: center;

    > img {
      align-self: center;
    }

    h2 {
      font-size: 24px;
      margin: 64px 0 24px;
      font-family: 'Poppins', sans-serif;
    }

    form {
      input {
        height: 50px;
        border-radius: 8px;
        padding: 0 16px;
        background: #fff;
        border: 1px solid #1b292f;
      }
      button {
        margin-top:  16px;
      }

      button, input {
        width: 100%;
      }
    }

    p {
      font-size: 14px;
      color: #535e64;
      margin-top: 16px;

      a {
        color: #0f327f;
      }
    }
  }

  .create-room {
    margin-top: 64px;
    height: 50px;
    border-radius: 8px;
    font-weight: 500;
    background: #ff4335;
    color: #fff;

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    border: 0;

    transition: filter 0.2s;

    img {
      margin-right: 8px;
    }

    &:hover {
      filter: brightness(0.9);
    }
  }

  .separator {
    font-size: 14px;
    color: #535e64;

    margin: 32px 0;
    display: flex;
    align-items:center;

    &::before {
      content: '';
      flex: 1;
      height: 1px;
      background: #535e64;
      margin-right: 16px;
    }
    &::after {
      content:'';
      flex: 1;
      height: 1px;
      background: #535e64;
      margin-left: 16px;
    }
  }

  
} 


